@import '../../styles/main.scss';

.about {
    flex-direction: column;
    width: 100%;

    .about-container {
        display: flex;
        gap: 20px;

        .about-photo {
            display: flex;
            align-items: center;
            flex-direction: column;
            justify-content: center;
            text-align: center;
            gap: 30px;

            .photo {
                width: calc(100% - 40px);
                max-width: 400px;
            }

            .about-social {
                display: flex;
                align-items: center;
                gap: 10px;

                .social-icons {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    width: 40px;
                    height: 40px;
                    border: 1px solid #131313;
                    border-radius: 50%;
                    color: #131313;
                    background-color: $co-body-bg;
                    cursor: pointer;
                    transition: transform 0.3s, box-shadow 0.3s;
                    box-shadow: $bs-sm;

                    &:hover {
                        transform: translateY(-5px);
                        box-shadow: $bs-md;
                    }
                }

                .btn-cv {
                    display: flex;
                    align-items: center;
                    gap: 10px;
                    border: 1px solid $co-primary;
                    border-radius: 4px;
                    padding: 10px 15px;
                    background-color: $co-body-bg;
                    cursor: pointer;
                    transition: transform 0.3s, box-shadow 0.3s;
                    box-shadow: $bs-sm;
                    font-family: $ff-p;
                    font-weight: $fw-semi;

                    a {
                        color: $co-primary;
                        text-decoration: none;
                    }

                    &:hover {
                        transform: translateY(-5px);
                        box-shadow: $bs-md;
                    }
                }
            }
        }

        .about-desc {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;

            p {
                line-height: 24px;
                color: $co-text;
            }
        }
    }

    @media screen and (max-width: $bp-md) {
        .about-container {
            display: flex;
            flex-direction: column;
        }
    }
}