@import "../../styles/main.scss";

footer {
    padding-top: 20px;
    background-color: $co-primary;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;

    .footer-container {
        height: 100%;
        width: calc(100% - 50px);
        max-width: $bp-sm;
        display: flex;
        justify-content: space-between;
        padding: 0 $sp-lg;

        .footer-left {
            display: flex;
            flex-direction: column;
            gap: 10px;

            .footer-logo {
                height: 26px;
                cursor: pointer;
            }

            .footer-social-icons {
                display: flex;
                gap: 15px;
                padding: 0;
                margin: 0;

                li {
                    list-style: none;

                    a {
                        color: #eee;
                        transition: color $td-3 linear;

                        &:hover {
                            color: $co-secondary;
                        }
                    }
                }
            }
        }

        .footer-nav {
            display: flex;
            flex-direction: column;
            gap: 15px;
            text-decoration: none;
            list-style: none;
            color: white;
            font-family: $ff-header;
            font-size: $fs-sm;
            font-weight: 300;
            margin: 0;
            cursor: pointer;
        }
    }

    .separator {
        margin: 20px 0;
        height: 1px;
        width: calc(100% - 40px);
        max-width: $bp-md;
        background-color: #eee;
    }

    .copyright {
        color: #ddd;
        font-family: $ff-header;
        font-size: $fs-xs;
        font-weight: 300;
        margin-bottom: 20px;
    }
}