@import '../../styles/main.scss';

.slider {
    width: 100%;
    height: auto;
    margin: auto;
    overflow: hidden;
    background-color: $co-primary;
    padding: 10px 0;

    .slide-track {
        display: flex;
        animation: scroll 30s linear infinite;
        -webkit-animation: scroll 40s linear infinite;
        width: calc(250px * 12);

        .slide {
            width: 250px;

            p {
                display: flex;
                color: white;
                font-size: $fs-lg;
                font-family: $ff-p;
                font-weight: $fw-normal;
                align-items: center;
                text-align: center;
                justify-content: center;
                gap: 10px;
            }
        }
    }

    @media screen and (max-width: 480px) {
        display: flex;
        align-items: center;
        justify-content: center;
        height: 40px;
    }
}

@keyframes scroll {
    0% {
        -webkit-transform: translateX(0);
        transform: translateX(0);
    }

    100% {
        -webkit-transform: translateX(calc(-250px * 6));
        transform: translateX(calc(-250px * 6));
    }
}