@import "../../styles/main.scss";

section {
    margin-top: $header-height;
    margin: 0;
    background-color: $co-body-bg;

    .home {
        display: flex;
        margin-top: $header-height;
        height: calc(100vh - 238px);
        gap: 50px;

        .home-desc {
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            justify-content: flex-start;
            padding-left: 40px;

            .header-name {
                font-weight: $fw-semi;
                margin: 0;
                color: $co-header;
            }

            .header-title {
                white-space: nowrap;
                height: 110px;
                margin-top: 0;
                margin-bottom: 20px;
                background: linear-gradient(90deg, $co-primary, #00505c);
                background-clip: border-box;
                -webkit-text-fill-color: transparent;
                -webkit-background-clip: text;
            }

            .header-subtitle {
                font-weight: $fw-semi;
                line-height: 30px;
                margin-top: 0;
                margin-bottom: 40px;
                color: $co-header;
            }

            .header-description {
                color: $co-text;
                font-weight: $fw-semi;
                line-height: 25px;
                max-width: 400px;
                margin: 0;
            }
        }

        .home-photo {
            display: flex;
            justify-content: center;
            align-items: center;
            width: 40%;

            .my-photo {
                z-index: 1;
                object-fit: contain;
                max-width: 380px;
            }

            .blob {
                position: absolute;
                overflow: hidden;
                max-width: 350px;
                width: 100%;
                max-height: 330px;
                height: 100%;
                border-radius: 42% 56% 72% 28% / 42% 42% 56% 48%;
                background-color: $co-primary;
                background-size: cover;
                background-position: center;
                animation: morph 3.75s linear infinite;

                @keyframes morph {

                    0%,
                    100% {
                        border-radius: 42% 56% 72% 28% / 42% 42% 56% 48%;
                    }

                    33% {
                        border-radius: 72% 28% 48% 48% / 28% 28% 72% 72%;
                    }

                    66% {
                        border-radius: 100% 56% 56% 100% / 100% 100% 56% 56%;
                    }
                }
            }
        }
    }

    @media screen and (max-width: 1024px) {
        .home .home-desc {
            padding: 0;

            .header-name {
                font-size: 30px;
            }

            .header-title {
                font-size: 70px;
            }

            .header-subtitle {
                font-size: 40px;
            }
        }
    }

    @media screen and (max-width: 912px) {
        .home {
            flex-direction: column-reverse;
            height: calc(100vh - 237px);

            .home-desc {
                height: 40%;
            }

            .home-photo {
                height: 60%;
                width: auto;

                .blob {
                    max-height: none;
                    max-width: none;
                    width: 50%;
                    height: 30%;
                }

                .my-photo {
                    max-width: none;
                    width: 70%;
                    object-fit: cover;
                }
            }
        }
    }

    @media screen and (max-width: $bp-sm) {
        .home {
            padding: 50px 20px;
            max-height: none;

            .home-desc {
                width: 100%;
            }
        }
    }

    @media screen and (max-width: $bp-xs) {
        .home {
            max-width: none;
            height: calc(100vh - 157px);
            justify-content: space-between;
            padding: 20px;
            gap: 30px;

            .home-desc {
                padding: 0;
                height: 50%;

                .header-name {
                    font-size: $fs-xl;
                }

                .header-title {
                    font-size: $fs-h3;
                    height: 70px;
                    margin-bottom: 10px;
                }

                .header-subtitle {
                    font-size: $fs-h4;
                    margin-bottom: 20px;
                }
            }

            .home-photo {
                height: 50%;
                width: 100%;

                .my-photo {
                    max-height: 100%;
                    object-fit: contain;
                }

                .blob {
                    max-width: 60%;
                    max-height: 200px;
                }
            }
        }
    }
}