@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;400;500;600;700;800;900&family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap');

/* =========== COLORS =========== */
$co-primary: #161927;
$co-secondary: #ffbd59;
$co-body-bg: #fbfbff;
$co-nav-bg: $co-body-bg;
$co-footer-bg: $co-primary;
$co-text: #6c6c6c;
$co-text-hover: #4b4b4b;
$co-header: #232323;

/* =========== FONTS =========== */
$ff-header: 'Poppins', sans-serif;
$ff-p: 'Montserrat', sans-serif;

$fs-xs: 12px;
$fs-sm: 14px;
$fs-md: 16px;
$fs-lg: 18px;
$fs-xl: 24px;
$fs-h5: 20px;
$fs-h4: 34px;
$fs-h3: 48px;
$fs-h2: 56px;
$fs-h1: 82px;

$fw-thin: 300;
$fw-normal: 400;
$fw-semi: 600;
$fw-bold: 700;

/* =========== SPACING =========== */
$sp-unit: 10px;
$sp-xs: calc(0.25 * $sp-unit);
$sp-sm: calc(0.5 * $sp-unit);
$sp-md: calc(1.5 * $sp-unit);
$sp-lg: calc(2.5 * $sp-unit);
$sp-xl: calc(5 * $sp-unit);

/* =========== HEIGHTS =========== */
$header-height: 60px;
$footer-height: 60px;
$logo-height: 25px;

/* =========== BORDERS =========== */
$bo-radius: 4px;

/* =========== SHADOWS =========== */
$bs-sm: 0 1px 2px #d3d3d3;
$bs-md: 0 4px 6px #d3d3d3;
$bs-lg: 0 10px 15px #d3d3d3;
$bs-xl: 0 20px 25px #d3d3d3;

/* =========== TRANSITIONS =========== */
$td-3: 0.3s;

/* =========== BREAKPOINTS =========== */
$bp-xs: 480px;
$bp-sm: 768px;
$bp-md: 1024px;
$bp-lg: 1200px;
$bp-xl: 1600px;
