body {
    padding: 0;
    margin: 0;
}

h1 {
    font-size: $fs-h1;
    font-family: $ff-header
}

h2 {
    font-size: $fs-h2;
    font-family: $ff-header;
}

h3 {
    font-size: $fs-h3;
    font-family: $ff-header;
}   

h4 {
    font-size: $fs-h4;
    font-family: $ff-p;
}

h5 {
    font-size: $fs-h5;
    font-family: $ff-header;
}

p {
    font-size: $fs-md;
    font-family: $ff-p;
}

section {
    display: flex;
    justify-content: center;
    align-items: center;
    overflow-y: hidden;
}

.container-lg {
    display: flex;
    justify-content: center;
    align-items: center;
    max-width: $bp-lg;
    width: 100%;
    padding: 50px 20px;
}

.col-2 {
    width: 50%;
}

.col-3 {
    width: 33%;
}

.col-4 {
    width: 25%;
}

.section-header {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 15px;
    margin-bottom: 60px;
    width: 100%;

    h4 {
        font-family: $ff-header;
        font-size: $fs-h4;
        color: $co-header;
        margin-top: 0;
        margin-bottom: 14px;
    }

    p {
        font-family: $ff-p;
        font-size: $fs-md;
        text-align: center;
        color: $co-text;
        font-weight: $fw-normal;
        margin: 0;
    }
}

@media screen and (max-width: $bp-md) {
    .col-2 {
        width: 100%;
    }
}

@media screen and (max-width: $bp-sm) {

}

@media screen and (max-width: $bp-xs) {
    h1 {
        font-size: $fs-h2;
    }

    h2 {
        font-size: $fs-h4;
    }

    h4 {
        font-size: $fs-xl;
    }

    p {
        font-size: $fs-md;
    }
}