@import "../../styles/main.scss";

section {
    .contact {
        flex-direction: column;
        width: 100%;

        .section-container {
            display: flex;
            width: 100%;
            margin-bottom: 150px;
            justify-content: center;
            align-items: center;
            gap: 30px;

            .contact-form {
                width: 100%;

                form {
                    display: flex;
                    flex-direction: column;

                    .container-contact {
                        display: flex;
                        gap: 20px;

                        .contact-left {
                            width: 50%;
                            display: flex;
                            flex-direction: column;

                            label {
                                font-family: $ff-p;
                                font-size: $fs-sm;
                                margin-bottom: 10px;
                                color: $co-text;
                            }

                            input {
                                font-family: $ff-p;
                                font-size: $fs-sm;
                                border-radius: 4px;
                                border: 1px solid #ccc;
                                padding: 10px;
                                margin-bottom: 15px;
                            }
                        }

                        .contact-right {
                            display: flex;
                            flex-direction: column;
                            width: 50%;

                            label {
                                font-family: $ff-p;
                                font-size: $fs-sm;
                                margin-bottom: 10px;
                                color: $co-text;
                            }

                            textarea {
                                font-family: $ff-p;
                                font-size: $fs-sm;
                                padding: 10px;
                                resize: none;
                                border-radius: 4px;
                                border: 1px solid #ccc;
                            }
                        }
                    }

                    .bt-submit {
                        background-color: $co-primary;
                        border-radius: 4px;
                        border: none;
                        font-family: $ff-p;
                        font-weight: $fw-semi;
                        cursor: pointer;
                        width: fit-content;
                        padding: 10px 20px;
                        margin-top: 20px;
                        color: white;
                        transition: transform 0.3s, box-shadow 0.3s;

                        .spinner {
                            width: 16px;
                            height: 16px;
                            border: 2px solid transparent;
                            border-top: 2px solid #fff;
                            border-radius: 50%;
                            animation: spin 1s linear infinite;
                            margin: 0 auto;
                        }

                        @keyframes spin {
                            0% {
                                transform: rotate(0deg);
                            }

                            100% {
                                transform: rotate(360deg);
                            }
                        }
                    }
                }
            }
        }
    }
}

.enviado-message {
    position: fixed;
    bottom: 20px;
    right: 60px;
    background-color: $co-secondary;
    color: #fff;
    padding: 10px 20px;
    border-radius: 5px;
    opacity: 1;
    transition: opacity 0.3s ease-in-out;
}

.enviado-message.show {
    opacity: 1;
    /* Mostrar el mensaje cuando la clase 'show' est√° presente */
}

@media screen and (max-width: 768px) {
    section .contact .section-container .contact-form form .container-contact {
        flex-direction: column;

        .contact-left,
        .contact-right {
            width: 100%;
        }
    }
}