@import "../../styles/main.scss";

.scroll-to-top-button {
    position: fixed;
    bottom: 20px;
    right: 20px;
    display: none;

    &.visible {
        transition: opacity 0.5s linear;
        display: block;
    }

    button {
        background-color: $co-secondary;
        color: white;
        border: none;
        opacity: 0.6;
        border-radius: 8px;
        padding: 10px 12px;
        cursor: pointer;
        transition: opacity 0.3s ease-in-out;

        &:hover {
            opacity: 1;
        }
    }
}