@import "../../../styles/main.scss";

.project-card {
    display: flex;
    flex-direction: column;
    border-radius: 12px;
    box-shadow: $bs-md;
    transition: transform 0.3s, box-shadow 0.3s;
    cursor: pointer;
    background-color: white;
  
    &:hover {
      box-shadow: $bs-lg;
      transform: translateY(-5px);
    }
  
    .card-img {
      height: 200px;
      object-fit: cover;
      background-color: gray;
    }
  
    .card-container {
      padding: 20px;
  
      .card-title {
        font-family: $ff-p;
        font-size: $fs-xl;
        color: $co-header;
        margin: 0;
      }
  
      .card-description {
        font-size: $fs-sm;
        line-height: 20px;
        font-family: $ff-p;
        color: $co-text-hover;
      }
  
      .card-icons {
        display: flex;
        gap: 10px;
  
        .card-chip {
          border: 1px solid #ccc;
          border-radius: 12px;
          width: fit-content;
          margin: 10px 0;
  
          p {
            margin: 0;
            padding: 6px 10px;
            font-family: $ff-p;
            font-weight: $fw-bold;
            font-size: 12px;
            color: $co-header;
          }
        }
      }
  
      .card-links {
        display: flex;
        justify-content: right;
        align-items: center;
        gap: 15px;
  
        a {
          color: $co-text-hover;
          transition: color $td-3 linear;
  
          &:hover {
            color: $co-text;
          }
        }
      }
    }
  }
  